import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { delPoint } from '../../actions';
import PointItem from '../PointItem';
import './PointsList.scss';

const PointsList = React.memo(({ pointData, removePoint }) => {
    const points = pointData.map((item) => {
        const { id, point } = item;
        return (
            <li key={id} className='points-list__item'>
                <PointItem point={point} removePoint={() => { removePoint(id); }} />
            </li>
        );
    });

    return (
        <ul className='points-list'>
            {points}
        </ul>
    );
});

PointsList.defaultProps = {
    pointData: [],
    removePoint: (f) => { return f; },
};
PointsList.propTypes = {
    pointData: PropTypes.array,
    removePoint: PropTypes.func,
};

const mapStateToprops = ({ pointData }) => {
    return {
        pointData,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        removePoint: (id) => {
            dispatch(delPoint(id));
        },
    };
};

export default connect(mapStateToprops, mapDispatchToProps)(PointsList);
