import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { addPoint } from '../../actions';
import './AddPointForm.scss';


const AddPointForm = React.memo(({ dispatch }) => {
    let point;
    const submit = (e) => {
        e.preventDefault();

        const coordinates = point.value;

        if (coordinates === '') {
            return null;
        }

        dispatch(addPoint(coordinates));

        point.value = '';

        return null;
    };

    return (
        <form
            className='add-point-form'
            method='post'
            onSubmit={submit}
        >
            <input
                className='add-point-form__point-input form-control'
                type='text'
                ref={(input) => {
                    point = input;
                }}
                placeholder='type new coordinates here'
            />
        </form>
    );
});

AddPointForm.defaultProps = {
    dispatch: (f) => { return f; },
};
AddPointForm.propTypes = {
    dispatch: PropTypes.func,
};

export default connect()(AddPointForm);
