import C from '../constants';
import getId from '../helpers';

export const addPoint = (point) => {
    return {
        type: C.ADD_POINT,
        payload: {
            point,
            id: getId(),
        },
    };
};

export const delPoint = (id) => {
    return {
        type: C.DELETE_POINT,
        payload: id,
    };
};
